import React from 'react';

class Searchbar extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <div className="searchbar">
        <img src={window.staticImages.magnifyingGlass} />
        <input
          type="text"
          placeholder="Search..."
          />
        <button>Search</button>
      </div>
    );
  }
}

export default Searchbar;
